import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import OperationModal from './components/./OperationModal'
import OperationModalChangeRecord from './components/./OperationModalChangeRecord'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.productAttributes,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({
      searchResult: [],
      searchParams: {},
      productCodeOptions: ''
    })
  }
  render() {
    const {
      menuPermissionIds
     } = this.props
    return (
      <section>
        <Bread route={['设置中心', '商品管理', '商品属性设置']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <Tool menuPermissionIds={menuPermissionIds}/>
        <Table menuPermissionIds={menuPermissionIds}/>
        {this.props.isShowModal ? <OperationModal /> : null}
        {this.props.isShowModal_changeRecord ? <OperationModalChangeRecord menuPermissionIds={menuPermissionIds}/> : null}
      </section>
    )
  }
}



