import React, { PureComponent } from 'react'
import { Modal, message, Button } from 'antd'
import { API_PRODUCTATTRIBUTES_IMPORT } from 'api'
import { Import } from 'HOC'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.productAttributes }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  /* 下载模版 */
  handleDownLoad = key => {
    const downloadData = JSON.parse(localStorage.getItem('b2bMmsDownload'))
    location.href = `${downloadData[key].downloadUrl}`
  }
  /* 导入成功 */
  handleImportSuccess = res => {
    const { handleGetList, searchParams, page, size, update } = this.props
    res && res.message === 'ok' && message.success('导入成功')
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  /* 报错导出 */
  handleExprotError = data => {
    const { handleGetList, searchParams, page, size, update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
    // 增加了部分成功导入的逻辑，导入失败之后刷新列表
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleExport({ searchParams: { page, size, ...reqObj } }, () => {
      message.success('导出成功')
    })
  }
  // 新增
  handleAdd = () => {
    const { update } = this.props
    update({ isShowModal: true, modalTitle: '商品属性维护', modalData: '' })
  }
  // 变更记录
  handleShowChangeRecord = () => {
    const { update } = this.props
    update({ isShowModal_changeRecord: true, modalTitle: '商品属性变更记录查询', modalData: '', searchParams_record: {} })
  }
  // 删除
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator,
      selectedRowKeys = [],
      update
    } = this.props
    if (selectedRowKeys && selectedRowKeys.length > 0) {
      confirm({
        title: '温馨提示',
        content: '是否进行删除操作？',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleOperator(
            {
              data: selectedRowKeys
            },
            () => {
              update({ selectedRowKeys: [] })
              message.success('操作成功')
              handleGetList({ searchParams: { page, size, ...searchParams } })
            }
          )
        }
      })
      return true
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    const { modalTuttonLoading, modalTuttonLoading_handleExport, menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds &&
          menuPermissionIds.includes('productAttributes_add') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleAdd}
            >
              新增
            </Button>
          )}
        {menuPermissionIds &&
          menuPermissionIds.includes('productAttributes_delete') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleOperator}
            >
              删除
            </Button>
          )}
          <Button
              type='primary'
              className={style.btn}
              icon='sync'
              disabled={modalTuttonLoading_handleExport}
              onClick={() => {
                this.handleDownLoad('T02')
              }}
            >
              下载模板
            </Button>
        {menuPermissionIds &&
          menuPermissionIds.includes('productAttributes_importBatch') && (
            <Import
              multiple={true}
              action={{ api: API_PRODUCTATTRIBUTES_IMPORT }}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const { update } = this.props
                update({
                  tableLoading: true,
                  modalTuttonLoading: true
                })
              }}
              onResponse={data => {}}
            >
              <Button
                type='primary'
                icon='upload'
                className={style.btn}
                loading={modalTuttonLoading}
                disabled={modalTuttonLoading_handleExport}
              >
                批量导入
              </Button>
            </Import>
          )}
        {menuPermissionIds &&
          menuPermissionIds.includes('productAttributes_export') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleExport}
              loading={modalTuttonLoading_handleExport}
            >
              导出
            </Button>
          )}
        {menuPermissionIds &&
          menuPermissionIds.includes('productAttributes_changeRecord') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleShowChangeRecord}
              loading={modalTuttonLoading_handleExport}
            >
              变更记录
            </Button>
          )}
      </div>
    )
  }
}



