import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_PRODUCTATTRIBUTES_PAGE,
  API_PRODUCTATTRIBUTES_CHANGE_RECORD,
  API_PRODUCTATTRIBUTES_DEL,
  API_PRODUCTATTRIBUTES_CREATE,
  API_PRODUCTATTRIBUTES_UPDATE,
  API_PRODUCTATTRIBUTES_EXPORT,
  API_PRODUCTATTRIBUTES_UNIT,
  API_PRODUCTATTRIBUTES_DETAIL
} from 'api'
//同步
export const update = object => ({
  type: actionType.PRODUCTATTRIBUTES_UPDATE,
  data: object
})

// 获取 - 商品属性列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.PRODUCTATTRIBUTES_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_PRODUCTATTRIBUTES_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
    })
    .catch(err => {
      console.error(err)
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_FAIL
      })
    })
}
// 删除 - 商品属性数据
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_PRODUCTATTRIBUTES_DEL, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_FAIL
      })
    })
}
// 获取 - 单个商品属性数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_PRODUCTATTRIBUTES_DETAIL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        locationCode = '',
        locationName = '',
        mrpPropKey = '',
        mrpPropValue = '',
        productCode = '',
        productName = ''
      } = result

      let dataFormater = {
        ...result,
        location: {
          value: `${locationCode} ${locationName}`,
          children: `${locationCode} ${locationName}`
        },
        mrpProp: {
          value: `${mrpPropKey} ${mrpPropValue}`,
          children: `${mrpPropKey} ${mrpPropValue}`
        },
        product: {
          value: `${productCode} ${productName}`,
          children: `${productCode} ${productName}`
        }
      }
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: {
          modalData: dataFormater
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_FAIL
      })
    })
}
// 获取 - 单个商品单位
export const getSingleUnit = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_PRODUCTATTRIBUTES_UNIT, ...reqData })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      let dataFormater = {
        unit: result,
        id: reqData.searchParams.id
      }
      console.log('dataFormater is', dataFormater)
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: {
          modalData: dataFormater
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_FAIL
      })
    })
}
// 创建/编辑 - 商品属性数据
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.PRODUCTATTRIBUTES_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_PRODUCTATTRIBUTES_UPDATE
      : API_PRODUCTATTRIBUTES_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.PRODUCTATTRIBUTES_UPDATE,
    data: { modalTuttonLoading_handleExport: true }
  })
  getData({ api: API_PRODUCTATTRIBUTES_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
}
// 获取 - 变更记录
export const getRecordList = reqData => dispatch => {
  dispatch({
    type: actionType.PRODUCTATTRIBUTES_UPDATE,
    data: { modalTuttonLoading_changeRecord: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_PRODUCTATTRIBUTES_CHANGE_RECORD, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '' } = result
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_UPDATE,
        data: {
          page,
          size,
          total,
          modalTuttonLoading_changeRecord: false,
          searchResultRecord: result
        }
      })
    })
    .catch(err => {
      console.error(err)
      dispatch({
        type: actionType.PRODUCTATTRIBUTES_FAIL
      })
    })
}



