import React, { PureComponent } from 'react'
import { Modal, Form } from 'antd'
import FormRecord from './FormRecord'
import TableRecord from './TableRecord'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  state => ({ ...state.productAttributes, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {

  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal_changeRecord: false, searchParams_record: {} })
  }
  render() {
    const {
      modalTitle,
      menuPermissionIds
    } = this.props
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={false}
        className='operationModal'
        footer={null}
        width='1000px'
      >
        <FormRecord menuPermissionIds={menuPermissionIds} />
        <TableRecord menuPermissionIds={menuPermissionIds}/>
      </Modal>
    )
  }
}
export default Form.create()(App)



