import React, { PureComponent } from 'react'
import { Form, Button, Row, Col } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxFormSearch } from 'Form'
import {
  API_GET_PRODUCTION_CODEANDNAME_MAJOR
} from 'api'
const FormItem = Form.Item
@connect(
  state => ({ ...state.productAttributes }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 查询数据
  query = () => {
    const { getRecordList, size, form } = this.props
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      if (!err) {
        console.log('reqObj is', reqObj)
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
          if (i == 'productCode') {
            reqObj.productCode = reqObj[i] && reqObj[i].split(' ')[0]
            console.log('form reqObj is111111', reqObj)
          }
        }
        getRecordList({ searchParams: { ...reqObj, page: 1, size } })
      }
    })
  }

  // 重置
  reset = () => {
    const { form } = this.props
    form.setFieldsValue({ productCode: '' })
  }
  render() {
    const {
      menuPermissionIds,
      form: { getFieldDecorator }
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <FormItem {...formItemLayout} label='商品编码/名称：'>
                {getFieldDecorator('productCode', {
                  // rules: [
                  //   // {
                  //   //   required: true,
                  //   //   message: '请填写!'
                  //   // }
                  // ]
                })(
                  <CsxFormSearch
                    dateApi={API_GET_PRODUCTION_CODEANDNAME_MAJOR}
                    showSearch={false}
                    searchParamsKey='code'
                    style={{}}
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                    onSelect={data => {
                      const { value = '' } = data
                      console.log('value is', value)
                    }}
                  />
                )}
              </FormItem>
          </Col>
          <Col span={8} push={8}>
            <Row type='flex' justify='end'>
              {menuPermissionIds &&
                menuPermissionIds.includes('productAttributes_find') && (
                  <Button
                    type='primary'
                    className={style.mr10}
                    onClick={this.query}
                  >
                    查询
                  </Button>
                )}
              {menuPermissionIds &&
                menuPermissionIds.includes('productAttributes_reset') && (
                  <Button type='primary' onClick={this.reset}>
                    重置
                  </Button>
                )}
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



