import React, { PureComponent } from 'react'
import { Table } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
@connect(
  state => ({ ...state.productAttributes, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: 'ID',
        width: 100,
        dataIndex: 'id',
        key: 'id'
      },
      {
        title: '地点编码',
        width: 150,
        dataIndex: 'locationCode',
        key: 'locationCode'
      },
      {
        title: '商品编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '单位',
        width: 80,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '评估类',
        width: 120,
        dataIndex: 'mrpValue',
        key: 'mrpValue',
        render: (text, record, index) => {
          const { mrpKey } = record
          return `${mrpKey} ${text}`
        }
      },
      {
        title: '操作类型',
        width: 80,
        dataIndex: 'operationType',
        key: 'operationType'
      },
      {
        title: '操作时间',
        width: 150,
        dataIndex: 'operationTime',
        key: 'operationTime'
      },
      {
        title: '操作人',
        width: 100,
        dataIndex: 'operationBy',
        key: 'operationBy'
      }
    ]
  }
  componentDidMount() {
    const { searchParams_record, getRecordList, page, size } = this.props
    getRecordList({ searchParams: { page, size, ...searchParams_record } })
  }
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { getRecordList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      getRecordList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams_record } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { getRecordList, searchParams_record, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        getRecordList({
          searchParams: {
            page,
            size,
            ...searchParams_record,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        getRecordList({
          searchParams: {
            page,
            size,
            ...searchParams_record,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams_record }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      getRecordList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      modalTuttonLoading_changeRecord,
      searchResultRecord
    } = this.props
    console.log('1111', modalTuttonLoading_changeRecord)
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={modalTuttonLoading_changeRecord}
          columns={this.columns}
          dataSource={searchResultRecord || []}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={false}
        />
      </div>
    )
  }
}



