import React, { PureComponent } from 'react'
import { Modal, Row, Col, Input, Form, Button, message } from 'antd'
// import {CsxFormSelect, CsxFormSearch} from 'Form'
import { CsxFormSelect, CsxFormSearch, CsxFormRadio } from 'Form'
import {
  API_GET_LOCATION_CODEANDNAME,
  API_GET_MRP_CODEANDNAME,
  API_GET_PRODUCTION_CODEANDNAME_MAJOR
} from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const FormItem = Form.Item
@connect(
  state => ({ ...state.productAttributes, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, getSingleDet } = this.props
    if (modalData) {
      //编辑
      const { id = '' } = modalData
      const params = {
        urlParams: [`${id}`]
      }
      getSingleDet(params, data => {})
    }
  }
  handleSubmit = e => {
    const { form, modalData, handleAddOrEdit } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      if (!err) {
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'location') {
              reqObj.location = reqObj[i].children
              console.log('form reqObj is111111', reqObj)
            }
            if (i == 'product') {
              reqObj.product = reqObj[i].children
            }
            if (i == 'mrpProp') {
              reqObj.mrpProp = reqObj[i].children
            }
          }
        }
        let result = {
            data: reqObj
          },
          msg = '创建成功'

        // 判断是否是修改,加ID

        if (modalData && modalData.id) {
          result.data.id = modalData.id
          msg = '编辑成功'
        }
        handleAddOrEdit({ ...result }, data => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  render() {
    const {
      modalTitle,
      form: { getFieldDecorator },
      modalTuttonLoading,
      getSingleUnit,
      modalData = {}
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    const statusRadioOPtions = [
      { key: 1, value: '启用' },
      { key: 0, value: '禁用' }
    ]

    const locationValue = this.props.form.getFieldValue('location') || modalData && modalData.location
    const productValue = this.props.form.getFieldValue('product') || modalData && modalData.product
    const { value: locationCodeAndName = '' } = locationValue || {}
    const { value: productCodeAndName = '' } = productValue || {}
    const extraParamsKey = { locationCode: locationValue && locationValue.value && locationValue.value.split(' ') && locationValue.value.split(' ')[0] }
    const mrpParamsKey = {
      locationCode: locationCodeAndName && locationCodeAndName.split(' ')[0],
      productCode: productCodeAndName && productCodeAndName.split(' ')[0]
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='地点编码/名称' hasFeedback>
                {getFieldDecorator('location', {
                  initialValue: modalData.location,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    dateApi={API_GET_LOCATION_CODEANDNAME}
                    label='地点编码/名称'
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                    onChangeSelect={data => {
                      this.props.form.setFieldsValue({ mrpProp: '' })
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='商品编码/名称'>
                {getFieldDecorator('product', {
                  initialValue: modalData.product,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSearch
                    disabled={locationValue ? false : true}
                    dateApi={API_GET_PRODUCTION_CODEANDNAME_MAJOR}
                    showSearch={false}
                    searchParamsKey='code'
                    extraParamsKey={extraParamsKey}
                    style={{}}
                    initData={modalData.product}
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                    onChangeSelect={data => {
                    }}
                    onSelect={data => {
                      const { value = '' } = data
                      const id = value && value.split(' ')[0]
                      const locationCode = locationCodeAndName && locationCodeAndName.split(' ')[0]

                      console.log(modalData, 1111)
                      this.props.form.setFieldsValue({ mrpProp: '', unit: '' })
                      getSingleUnit(
                        {
                          searchParams: {
                            locationCode,
                            id: modalData.id,
                            productCode: id
                          }
                        }, (data) => {
                        this.props.form.setFieldsValue({ unit: data && data.result })
                      })
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='单位' hasFeedback>
                {getFieldDecorator('unit', {
                  initialValue: modalData.unit,
                  rules: [
                    {
                      // required: true,
                      // pattern: /(^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1})?)$/,
                      // message: '请输入正数且不超过一位小数!'
                    }
                  ]
                })(<Input disabled/>)}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='评估类' hasFeedback>
                {getFieldDecorator('mrpProp', {
                  initialValue: modalData.mrpProp,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    disabled={productValue && locationValue ? false : true}
                    searchParams={mrpParamsKey}
                    allowClear={true}
                    dateApi={API_GET_MRP_CODEANDNAME}
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='件装数' hasFeedback>
                {getFieldDecorator('piectQty', {
                  initialValue: modalData.piectQty,
                  rules: [
                    {
                      // required: true,
                      pattern: /(^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1})?)$/,
                      message: '请输入正数且不超过一位小数!'
                    }
                  ]
                })(<Input />)}
              </FormItem>
            </Col>
          </Row>
          {/* <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='状态'>
                {getFieldDecorator('status', {
                  initialValue:
                    modalData.status === 0 ? 0 : modalData.status || 1,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(<CsxFormRadio data={statusRadioOPtions} />)}
              </FormItem>
            </Col>
          </Row> */}
          <Row type='flex' justify='center'>
            <Col>
              <Button
                style={{ marginRight: '48px' }}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type='primary'
                htmlType='submit'
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



